<template>
    <div class='PicList'>
        <img v-for="(it,i) in url" :key='i' v-lazy="it" alt="">
    </div>
</template>
<script>
export default {
    name:'PicList',
    data(){
        return{
            pic_list:'',
            url:new Array()
        }
    },
    created(){
        if(!this.$route.query){
            this.$router.back();//可以自己用vuex
        }
        this.axios.get('/car_api/v2-car-getCategoryImageList.html?SerialID='+
            this.$route.query.SerialID+ "&ImageID=" +
            this.$route.query.ImageID +
            "&Page=" + 1 + "&PageSize=30&_1531054352382")
        .then(res => {
            if(res.status ===200 && res.data.code === 1){
                this.pic_list = res.data.data;
                console.log(res.data.data);
                this.pic_url(res.data.data)
            }
        })
    },
    methods:{
        pic_url(){
            this.pic_list.List.forEach(e => {
                let arr = e.Url.split('{0}');
                this.url.push(arr[0] + 1 + arr[1]);
            });
        }
    }
}
</script>
<style lang='stylus' scoped>
    .PicList
        position absolute
        top 0
        left 0
        width 100%
        height 100%
        background-color white
        z-index 32
        overflow auto
        img 
            width 32%
            margin 2px
            height 76px
</style>